<style scoped lang="scss">@import "../style/style.scss";
.Socila-Contacts {
    position: fixed;
    left: 30px;
    top: 50%;
    transform: translate(0,-50%);
    z-index: zIndex(socialcontact);
    display: flex;
    flex-direction: column;
    z-index: zIndex(default);
    @media (max-width: 1300px) {
        position: absolute;
        width: 100%;
        top: initial;
        left: 50%;
        bottom: 30px;
