        animation-name: show;
        animation-fill-mode: forwards;
        animation-duration: 0.3s;
        &:hover {
            img {
                filter: grayscale(0%);
                opacity: 1;
            }
        }
        @for $i from 0 through 3 {
            &:nth-child(#{$i}) {
                animation-delay: calc(calc(#{$i} + 30) * 0.125s);
            }
            @media (max-width: 500px) {
                &:nth-child(#{$i}) {
                    position: absolute;
                    left: calc( 12.5% * 2 * (#{$i}) - 55px / 2);
                }
            }
