<template>
<section class="About">
	<BgLines :clearcanvas="clearcanvas" :taxifusion="taxifusion" :lumencase="lumencase" :slected="slected"></BgLines>
	<div class="Logo ">
		<img src="/../../static/logo.svg" alt="Logo ">
	</div>
	<div class="Section-Wrapper">
		<span>Introduction</span>
		<h1>
			<span>Gobie&nbsp;</span><span>Nanthakumar&nbsp;</span><span>—&nbsp;</span><span>webdeveloper&nbsp;</span><span>from&nbsp;</span><span>Germany</span><span>,&nbsp;</span><span>currently&nbsp;</span><span>working&nbsp;</span><span>for&nbsp;</span><a href="http://www.clear-canvas.com/" target="_blank" class="ClearCanvas" :class="{ 'isWhite': clearcanvas}" v-on:mouseover="clearcanvas = !clearcanvas" v-on:mouseleave="clearcanvas = !clearcanvas">ClearCanvas&nbsp;</a><span>in&nbsp;</span><span>lovely&nbsp;</span><span>Berlin</span><span>.&nbsp;</span>
		</h1>
		<h2>
			<span>Projects&nbsp;</span><span>I&nbsp;</span><span>am&nbsp;</span><span>working&nbsp;</span><span>on</span><span>:&nbsp;</span><a :class="{ 'isWhite': taxifusion}" v-on:mouseover="taxifusion = !taxifusion" v-on:mouseleave="taxifusion = !taxifusion" href="http://www.taxifusion.de " target="_blank " class="Taxifusion ">TaxiFusion</a><span>,&nbsp;</span><a :class="{ 'isWhite': lumencase}" v-on:mouseover="lumencase = !lumencase" v-on:mouseleave="lumencase = !lumencase"  href="http://www.lumencase.com" target="_blank" class="Lumencase">Lumencase&nbsp;</a><span>and&nbsp;</span><a :class="{ 'isWhite': slected}" v-on:mouseover="slected = !slected" v-on:mouseleave="slected = !slected" href="http://www.slected.me " target="_blank " class="Slected ">Slected&nbsp;</a>
		</h2>
	</div>
</section>

</template>

<script>
import BgLines from '../components/BgLines.vue';

export default {
	name: 'About',
	props: {
		ButtonText: {
			type: String,
			default () {
				return ""
			}
		},
	},
	components: {
		BgLines
	},
	data() {
		return {
			clearcanvas: false,
			taxifusion: false,
			lumencase: false,
			slected: false
		}
	},
	created() {

	},
	watch: {

	},
	computed: {

	},
	methods: {
		changeBg: function (cases) {
			switch (cases) {
				case "clearcanvas":
					this.clearcanvas = true;
					break;
				default:

			}
		}

	},

	mounted() {

	}
}

</script>

<style lang="scss">@import "../style/style.scss";
$elements: 15;

.About {
    width: 100%;
    height: 100vh;
    background-color: color(bg-start);
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    perspective: 1000px;

    &::after {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        right: 0;
        height: 5px;
        background: linear-gradient(45deg,color(prime),color(second));
        background-size: 400% 400%;
        animation: bgAnimation 2s ease infinite;
        z-index: zIndex(default)+1;
    }
    &::before {
        content: "about";
        position: absolute;
        font-size: 550px;
        font-weight: 700;
        color: color(text-lighter);
        z-index: zIndex(default)-1;
        pointer-events: none;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
    }
    @media (max-width: 1420px) {
        &::before {
            font-size: 450px;
        }
    }
    @media (max-width: 1200px) {
        &::before {
            font-size: 350px;
        }
    }
    @media (max-width: 800px) {
        &::before {
            font-size: 250px;
        }
    }
    @media (max-width: 500px) {
        &::before {
            font-size: 120px;
        }
    }
    .Logo {
        position: absolute;
        top: 0;
        right: 0;
        margin: 35px 30px;
        z-index: zIndex(logo);
        img {
            display: block;
            width: 60px;
            height: 60px;
            object-fit: contain;
            @media (max-width: 800px) {
                width: 30px;
                height: 30px;
            }
        }
    }
    .Section-Wrapper {
        max-width: 1000px;
        margin: 0 auto;
        padding: 25px;
        box-sizing: border-box;
        z-index: zIndex(default)+1;
        position: relative;
        > span {
            font-weight: 600;
            color: color(text-light);
            display: block;
            display: inline-block;
            transform: translateY(30px) rotateX(40deg) rotateZ(5deg);
            opacity: 0;
            animation-fill-mode: forwards;
            animation-duration: 0.3s;
            animation-name: show;
            animation-delay: calc(17 * 0.125s);

        }

    }

    h1 {
        font-size: 70px;
        color: color(text);
        margin: 0;
        @media (max-width: 1200px) {
            font-size: 50px;
        }
        @media (max-width: 800px) {
            font-size: 35px;
        }
        @media (max-width: 500px) {
            font-size: 30px;
            line-height: 45px;
        }
        a,
        span {
            display: inline-block;
            transform: translateY(30px) rotateX(40deg) rotateZ(5deg);
            opacity: 0;
            animation-name: show;
            animation-fill-mode: forwards;
            animation-duration: 0.3s;
            &.ClearCanvas {
                cursor: pointer;
                color: color(clearcanvas);
                transition: all 0.5s ease;
                transition-delay: 0.2s;

                &.isWhite {
                    color: #fff;
                }
            }
            @for $i from 0 through $elements {
                &:nth-child(#{$i}) {
                    animation-delay: calc(calc(#{$i} + 17) * 0.125s);
                }
            }
        }
    }
    h2 {
        margin-top: 50px;
        font-size: 36px;
        font-weight: 600;
        @media (max-width: 1200px) {
            font-size: 30px;
        }
        @media (max-width: 800px) {
            font-size: 25px;
        }
        @media (max-width: 500px) {
            font-size: 20px;
            margin-top: 30px;
        }
        a,
        span {
            display: inline-block;
            &.Lumencase,
            &.Slected,
            &.Taxifusion {
                cursor: pointer;
                transition: all 0.5s ease;
                transition-delay: 0.2s;
                &.isWhite {
                    color: #fff;
                }
            }
            &.Taxifusion {
                color: color(taxifusion);
            }
            &.Lumencase {
                color: color(lumencase);
            }
            &.Slected {
                color: color(slected);
            }
            transform: translateY(30px) rotateX(40deg) rotateZ(5deg);
            opacity: 0;
            animation-name: show;
            animation-fill-mode: forwards;
            animation-duration: 0.3s;
            &.ClearCanvas {
                cursor: pointer;
                color: color(clearcanvas);
            }
            @for $i from 0 through $elements {
                &:nth-child(#{$i}) {
                    animation-delay: calc(calc(#{$i} + 17) * 0.125s);
                }
            }
        }
    }
    .Bg-Line {
        @media (max-width: 800px) {
            &::after {
                background-color: rgba(#5121E6,0.07);
            }
        }
        @media (max-width: 500px) {
            &::after {
                background-color: rgba(#5121E6,0.07);
            }
        }
    }
    @keyframes show {
        100% {
            opacity: 1;
            transform: rotate(0) translateY(10px);
        }
    }
    @keyframes bgAnimation {
        0% {
            background-position: 0 50%;
        }
        50% {
            background-position: 100% 50%;
        }
        100% {
            background-position: 0 50%;
        }
    }
}
</style>



